* {
  border: 1px dashed red; }

$project-grey: #BDBBBB;
$project-pink: #F77EB5;

body {
  position: relative;
  font-family: "Helvetica Neue",Helvetica,Arial,sans-serif;
  font-size: 18px;
  nav {
    background: $project-grey; }
  nav .navLinks {
    a {
      color: Black; } } }

hr {
  clear: both;
  margin: 0;
  border: none; }

body > main {
  nav {
    position: absolute;
    height: 70px; } }

section {
  margin-top: 20px;
  margin-bottom: 20px; }

h2 {
  text-align: center;
  margin-bottom: 30px;
  font-family: 'Lobster', cursive;
  font-size: 34px; }

h3 {
  text-align: center;
  margin-bottom: 30px;
  font-family: 'Lobster', cursive;
  font-size: 24px; }

footer {
  height: 200px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: $project-grey;
  li {
    height: auto; }
  a {
    padding: 15px; } }

.navLinks {
  width: 100%;
  background: transparent;
  .logo {
    padding-left: 15px;
    padding-top: 15px;
    display: inline-block;
    float: left; }
  & > ul {
    padding: 15px;
    list-style: none;
    display: inline-block;
    float: right;
    margin: 0; } }

.menu {
  color: white;
  font-family: 'Lobster', cursive;
  font-size: 24px; }

.menuOption {
  height: auto;
  padding: 5px;
  font-family: 'Lobster', cursive;
  font-size: 40px;
  margin: 0;
  width: 100%;
  a {
    color: black; }
  & a:hover {
    text-decoration: none; } }

.slideup, .slidedown {
  max-height: 0;
  overflow-y: hidden;
  -webkit-transition: max-height 0.5s ease-in-out;
  -moz-transition: max-height 0.5s ease-in-out;
  -o-transition: max-height 0.5s ease-in-out;
  transition: max-height 0.5s ease-in-out; }

.slidedown {
  max-height: 600px ; }

.intro {
  background-image: url(../img/perfil2.jpg);
  background-size: cover;
  margin: 0;
  width: 100%;
  height: 500px;
  div {
    margin-top: 200px;
    p {
      font-family: 'Marck Script', cursive;
      font-size: 30px;
      width: 70%;
      margin: auto;
      text-align: center;
      color: white; } } }


.hide {
  display: 'none'; }

