  height: 500px;
  div {
    margin-top: 200px;
    p {
      font-family: 'Marck Script', cursive;
      font-size: 40px;
      width: 70%;
      margin: auto;
      text-align: center;
      color: white; } } }

.row.item {
  margin-right: 15px;
  margin-left: 15px; }


/************** Repsonsive elements *********************/

// x-small devices

@media (max-width: 575px) {
  .intro {
    div {
      p {
        font-size: 22px; } } } }

// small devices

@media (min-width: 576px) and (max-width: 768px) {
  .intro {
    div {
      p {
        font-size: 26px; } } } }

